body {
    height: 100vh;
}

.text-secondary {
    color: $primary-color-light !important;
}

.off-canvas .off-canvas-content {
    padding: 0;
}

.off-canvas .off-canvas-sidebar {
    overflow: visible;
    min-width: inherit;
}

.col-auto {
    max-width: 100%;
}

.form-input.input-xl {
    font-size: 1.3rem;
    height: 2.8rem;
    padding: 0.35rem 0.48em;
}

.hero-center {
    height: 100%;
    justify-content: center;
}

.hero {
    .hero-body.hero-body-large {
        width: 300px;
        margin: 6rem;
    }
}

.language-selector {
    position: absolute;
    top: 15px;
    right: 15px;
}

.off-canvas-content {
    #main {
        display: flex;
        flex-wrap: nowrap;
        flex-direction: row;
    }

    .panel {
        border-left: none;
        min-width: 300px;
        // width: 600px;
        position: relative;
        border-radius: 0;

        .breadcrumb {
            margin: 0;
            padding: 0;
        }

        .panel-header {
            padding: .98rem;
            border-bottom: 0.05rem solid #dadee4;

            .panel-header-title {
                position: relative;

                .panel-header-title-right {
                    position: absolute;
                    right: 1rem;
                    top: .4rem;
                }
            }
        }

        .panel-nav {}

        .panel-body {
            padding: 0;

            .panel-block {
                padding: 2rem;
                border-bottom: 1px dashed #e4e7eb;
            }

            .panel-block:last-child {
                border-bottom: none !important;
            }

            .panel-body-content {
                position: relative;
                z-index: 0;
            }
        }

        .panel-footer {
            padding: 0.88rem;
            border-top: 0.05rem solid #dadee4;
        }
    }

    @media screen and (max-width:960px) {
        .column {
            max-width: 100%;
            width: 100%;
        }

        .panel {
            min-width: 100%;

            .breadcrumb {
                margin-top: -0.5rem;
                margin-left: 2rem;
                margin-bottom: .8rem;
            }
        }
    }
}

.logo {
    white-space: nowrap;
    text-decoration: none;
    outline: none;
    display: block;
    padding: 20px;
    width: 200px;

    &:hover,
    &:focus {
        outline: none;
        text-decoration: none;
        box-shadow: none;
    }

    .logo-picto {
        display: inline-block;
        vertical-align: middle;
        width: 20%;
    }

    .logo-text {
        display: inline-block;
        vertical-align: middle;
        width: 80%;
        padding: 0 10px;
    }
}

.custom-icon {
    display: inline-block;

    >svg {
        stroke: currentColor;
        stroke-width: 2px;
        stroke-linecap: round;
        stroke-linejoin: round;
        fill: none;
        box-sizing: border-box;
        display: inline-block;
        font-size: inherit;
        font-style: normal;
        height: 1em;
        margin-top: -0.2em;
        position: relative;
        text-indent: -9999px;
        vertical-align: middle;
        width: 1em;
    }
}

.menu {
    padding: 0;
    min-width: inherit;

    .menu-item {
        a {
            margin: 0 -0.2rem;

            .menu-item-title {
                margin: 0 0.4rem;
            }
        }
    }
}

.toolbar {
    overflow: visible;
    box-shadow: 0px 0px 6px 0px rgb(0 0 0 / 10%);

    .panel-head {
        border-bottom: 1px solid #dadee4;
        height: 3.6rem;
        margin-bottom: .9rem;
    }

    .panel-body-end {
        padding: 0 0.8rem;
    }

    .panel-footer {
        border-top: 1px solid #dadee4;
        height: 3.6rem;
        margin-top: .9rem;
    }

    #toolbar-switch {
        position: absolute;
        right: -.3rem;
        top: 1.5rem;
        opacity: 0;

        &:hover {
            opacity: 1;
        }
    }

    .tooltip::after {
        display: none;
    }

    #toolbar-switch:checked+.panel {
        min-width: 75px;
        width: 75px;
        overflow: visible;

        .panel-head {
            overflow: hidden;
        }

        .panel-body {
            overflow: visible;
        }

        .logo-text {
            display: none;
        }

        .menu-item-title {
            display: none;
        }

        .menu {
            padding: 0;
            min-width: inherit;

            .menu-item>a {
                margin: 0 -0.2rem;
            }

            .tooltip::after {
                display: block;
            }
        }
    }
}

.preview-frame {
    border: none;
    height: 100%;
    overflow: hidden;
}

.viewer-frame {
    border: none;
    height: 100%;
}

.form-help {
    color: #bcc3ce;
    font-size: .7rem;
    margin-top: .2rem;
}

.notif {
    position: absolute;
    z-index: 100;
    top: 1rem;
    right: 1rem;
}

#main.expand-panel {
    #panel {
        width: calc(100vw - 147px)!important;
        .resizer {
            width: 5px;
            height: 100%;
            background: #5c5edb;
            position: absolute;
            z-index: 100;
            right: 0;
            top: 0;
            bottom: 0;
            cursor: ew-resize;
        }
    }
}
#main.expand-viewer {
    #panel {
        width: 5px!important;
        .resizer {
            width: 5px;
            height: 100%;
            background: #5c5edb;
            position: absolute;
            z-index: 100;
            right: 0;
            top: 0;
            bottom: 0;
            cursor: ew-resize;
        }
    }
}

#viewer{
    position:relative;
}
