#panel {
    position: relative;

    .resizer {
        width: 1px;
        height: 100%;
        background: transparent;
        position: absolute;
        z-index: 100;
        right: 0;
        top: 0;
        bottom: 0;
        cursor: ew-resize;

        &:hover {
            width: 5px;
            height: 100%;
            background: #5c5edb;
            position: absolute;
            z-index: 100;
            right: 0;
            top: 0;
            bottom: 0;
            cursor: ew-resize;

        }

    }

}

.scroll-shadow-content {
    overflow-x: hidden;
    overflow-y: auto;

    &::-webkit-scrollbar-track {
        background-color: #ddd;
    }

    &::-webkit-scrollbar {
        width: .2rem;
        background-color: #ddd;
    }

    &::-webkit-scrollbar-thumb {
        background-color: rgb(189, 189, 189);
    }
}